<template>
    <vue-scroll>
        <div class="card">
            <div class="content-top">
                <div class="left mainBgColor">
                    <user-card v-bind:info="info" v-bind:isCreateVip="isCreateVip"></user-card>
                </div>
                <router-link :to="{name:'activeCard'}" v-if="isCreateVip ===1 && isHasCard === 0 && getCard === 1"><el-button class="btn" size="small" type="primary">{{ $L('立即激活') }}</el-button></router-link>
            </div>
            <div class="content-middle" v-if="info && info.hasOwnProperty('nowEXP') && info.hasOwnProperty('nexEXP')">
                <div class="text-box">
                    <i class="bbx-font bbx-icon-huiyuan1"></i>
                    <span>{{ isHasCard == 0 ? '&nbsp;': info.groupName}}</span>
                    <span>
                        <i v-if="info.isAssign === 1">

                        </i>
                        <i v-else>
                            <i v-if="info.nexEXP === 0">
                                {{ $L('已达到最高级') }}
                            </i>
                            <i v-else-if="info.nexEXP !== 0 && isHasCard !== 0">
                                ，{{ $L('距离下一级还差') }}{{ info.nexEXP }}{{ $L('经验值') }}
                            </i>

                        </i>
                    </span>
                </div>
                <el-progress :show-text="false" :percentage="info.nowEXP<=0 ? 0 : info.nowEXP/(info.nowEXP+info.nexEXP)*100" color="#CA76B8"></el-progress>
                <div class="exp">
                    <span style="float: left">EXP.{{ info.nowEXP }}</span>
                    <span  v-if="hasUserCard == 1" class="span-right">
                        <i>
                            {{ nexEXP === 0 ? '' :   $L('EXP') + Number(nowEXP+nexEXP)  }}
                        </i>
                    </span>
                </div>
            </div>
            <div class="content-bottom">
                <div class="bottom-item" v-for="(item,index) in menu" :key="index" @click="gotoRouter(item)">
                    <span style="float: left">{{item.title}}</span><span style="float: right;font-size: 16px;" class="bbx-font bbx-icon-iconfontjiantou2"></span>
                </div>
            </div>
        </div>
    </vue-scroll>
</template>

<script>
    import  sectionTitle from "../../../../components/common/sectionTitle.vue"
    import userCard from "../../../../components/coupon/userCard.vue"
    export default {
        name: "card",
        components:{
            userCard,sectionTitle
        },
        data () {
            return {
                menu:[
                    {
                        title:this.$L('会员卡详情'),
                        router:'detailsMobile'
                    },
                    {
                        title:this.$L('经验记录'),
                        router:'recordMobile'
                    }
                ],
            }
        },
        props : {
            routerData : {
                type : Object,
                default:()=>{
                    return {};
                }
            }
        },
        computed:{
            info:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('info')?this.routerData.data.info:{};
                }
            },
            isCreateVip:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('isCreateVip')?this.routerData.data.isCreateVip:0;
                }
            },
            isHasCard:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('isHasCard')?this.routerData.data.isHasCard:'';
                }
            },
            getCard:{
                get(){
                    return this.routerData.hasOwnProperty('data') && this.routerData.data.hasOwnProperty('getCard')?this.routerData.data.getCard:'';
                }
            },
            hasUserCard:{
                get() {
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('hasUserCard') && this.cardInfo.hasUserCard === 1) {
                        return true;
                    } else {
                        return false;
                    }
                }
            },
        },
        methods : {
            gotoRouter(item) {
                if (item && item.hasOwnProperty('router')) {
                    let params = {};
                    if (item.hasOwnProperty('params')) {
                        Object.assign(params, item.params);
                    }
                    if (item.hasOwnProperty('paramsData')) {
                        Object.assign(params, item.paramsData);
                    }
                    this.$router.push({name: item.router, params: params});
                } else if (item && typeof item.func === "function") {
                    if (item.hasOwnProperty('funcData')) {
                        item.func(item.funcData);
                    } else {
                        item.func();
                    }
                }
            }

        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../../assets/icon/iconfont.css";
    @import "../../../../assets/css/common/common";
    .card{
        width: 100%;
        margin-bottom: 50px;
        .content-top{
            background: #fff;
            padding-bottom: 20px;
            margin-bottom: 10px;
            padding-top: 20px;
            .left{
                width: 310px;
                height: 180px;
                border-radius: 20px;
                margin: 0 auto;
                /deep/ .user-card{
                     position: relative;
                     overflow: hidden;
                     top: 0;
                     left: 0;
                     width: 310px;
                     height: 180px;
                     padding: 25px;
                }
            }
            .btn{
                display: block;
                width: 120px;
                height: 40px;
                margin: 20px auto 0;
            }
        }
        .content-middle{
            padding: 0 4% 5px;
            background-color: #fff;
            margin-bottom: 10px;
            color: #999999;
            .text-box{
                overflow: hidden;
                margin-bottom: 5px;
                .icon{
                    padding-left: 20px;
                    vertical-align: text-bottom;
                }
                .tips{
                    float: right;
                    margin-top: 17px;
                }
            }
            .exp{
                margin-top: 5px;
                overflow: hidden;
            }
            .bbx-icon-huiyuan1{
                color: #FCD23D;
                font-size: 26px;
            }
        }
        .content-bottom{
            background-color: #fff;
            .bottom-item{
                padding: 0 4%;
                overflow: hidden;
                line-height: 60px;
                font-size: 14px;
                border-bottom: 1px solid #E1E1E1;
            }
        }
    }
</style>